
header {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    /* height: 4.5rem; */
    min-height: 3.5rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: var(--header-color);
    border-bottom: 0.13rem solid var(--header-border-color);
    border-top: 0.13rem solid var(--header-border-color);
    z-index: 100;
    flex-wrap: wrap;
    padding: 0.5rem 0;
    /* backdrop-filter: blur(1rem); */
    backdrop-filter: blur(0.3rem); 
   
    
}

header .logos {
    min-height: 2rem;
    min-width: 10%;
    display: flex;
    justify-content: end;
    align-items: center;

}

header>.logos>.logo-a>.logo-top {

    height: 40%;
    width: 100%;
}

header>.logos>.logo-a>.logo-buttom {

    height: 60%;
    width: 100%;
}

header>.logos>.logo-a {
    display: flex;
    height: 2rem;
    flex-direction: column;
    justify-content: end;
    justify-items: stretch;
    align-items: stretch;
}

header>.logos a {
    text-decoration: none;
    font-weight: 500;
    font-size: 1.1rem;
    line-height: 1.2;
    text-align: center;
    color:var(--header-font-color);
    letter-spacing: 0.0rem;

}

header .logos .image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.5rem;
    height: 2.5rem;
}

header .logos .image>img {
    height: 110%;
}

header>.navigation-simplify {
    display: none;
    position: relative;
    height: 3rem;
    width: 10%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    justify-items: stretch;
    text-align: center;
   
}

header>.navigation-simplify>div {
    position: absolute;
    left: 0rem;
    top: 3rem;
    display: none;
    min-height: 3.6rem;
    width: 15rem;
    background: var(--header-color);
    border: 0.1rem solid var(--header-border-color);
    border-radius: 0.5rem;
    text-align: center;

}

header>.navigation-simplify:hover>div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    animation-name: fade-in;
    /*动画名称*/
    animation-duration: 0.6s;
    /*动画持续时间*/
    -webkit-animation-name: fade-in;
    /*针对webkit内核*/
    background: var(--banner-hover-color);


}

header>.navigation-simplify:hover {
    background: rgba(114, 120, 158, 0.5);
    border-radius: 0.8rem;
}


header>.navigation-simplify svg {
    fill: var(--header-svg);
    font-size: 1.7rem;
}


header .navigation-simplify>div>div {
    margin: 0.6rem 0rem;
    width: 13rem;
    height: 3.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0.6rem;
    
}

header .navigation-simplify>div>div>div {
    width: 85%;
    border-left: 0.3rem solid var(--header-border-color);
    
}

header .navigation-simplify>div>div:hover {
    background: var(--header-hover-in-color);
}

header>.navigation {

    height: 100%;
    min-width: 35%;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    justify-items: stretch;
    align-items: center;
    text-align: center;
    /* background: blue; */
    /* background: red; */

}

header>.navigation>div {

    position: relative;
    padding: 1rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: stretch;
    flex-wrap: wrap;
    min-width: 13%;
    height: 3rem;
    
    z-index: 300;
    text-align: center;

}

header a {

    text-decoration: none;
    color:var(--header-font-color);
    font-weight: 250;
    font-size: 1.3rem;
    letter-spacing: 0.3rem;
    /* background: red; */
    font-weight: 500;

}

header a:hover {

    color:var(--header-font-color);

}

header .navigation>div:hover {

    background: var(--header-hover-color);
    border-radius: 0.8rem;
}

header .navigation>div:hover>div {
    /* visibility: visible; */
    /* display: inline-block; */
    display: flex;
}

header .navigation>div>div {

    display: none;
    position: absolute;
    left: 0rem;
    top: 3rem;
    min-height: 4rem;
    width: 15rem;
    background: var(--header-hover-color);
    border: 0.1rem solid var(--header-border-color);
    border-radius: 0.5rem;
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    animation-name: fade-in;
    /*动画名称*/
    animation-duration: 0.6s;
    /*动画持续时间*/
    -webkit-animation-name: fade-in;
    /*针对webkit内核*/
}

header .navigation>div>div>div {
    margin: 0.6rem 0rem;
    width: 13rem;
    height: 4rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0.6rem;
}

header .navigation>div>div>div>div {
    width: 85%;
    border-left: 0.3rem solid var(--header-border-color);
    display: flex;
    flex-direction: column;
    justify-items: stretch;
    align-items: stretch;


}


header .navigation>div>div>div:hover {
    background:var(--header-hover-in-color);
}

header .search-s {
    min-width: 30%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: start;


}

header .search {
    width: calc(20vw + 4rem);
    height: 2.2rem;
    border: 0.1rem solid var(--header-srarch-border-color);
    border-radius: 25px;
    padding: 0rem 1rem;
    display: flex;
    justify-content: space-between;
    /* background: var(--header-color); */
}

header .search>.input- {
    width: 20vw;
    min-width: 1rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;


}

header .search .search-images {
    width: 4rem;
    display: flex;
    justify-content: space-between;

}

header .search .image- {


    display: flex;
    justify-content: center;
    align-items: center;
}

header .search .image- svg {

    fill: var(--header-svg);

}

header>.search .image- svg:active {

    fill: rgb(255, 169, 71);

}


header .search>.input->input::placeholder {

    font-size: 1.1em;
    color:var(--header-font-color);
}

header .search .search-complex {

    display: flex;
    justify-content: center;
    align-items: center;
    fill: var(--header-svg);

}

header .search .search-complex svg {

    fill: var(--header-svg);
}

header .search .search-complex svg:active {

    fill: rgb(255, 169, 71);
}



header .search>.input->input {

    width: 100%;
    height: 80%;
    outline: none;
    border: 0;
    background: none;
    color:var(--header-font-color);
    margin: 0.1rem 0.1rem;

}






header>.userHidden {
    display: none;
}



header>.user {

    width: 5%;
    height: 3.2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    justify-items: stretch;
    text-align: center;
  

}

header>.user>div {
    position: absolute;
    right: 0rem;
    top: 3rem;
    display: none;
    min-height: 4rem;
    min-width: 9rem;
    background: var(--header-color);
    border: 0.1rem solid var(--header-border-color);
    border-radius: 0.5rem;
    text-align: center;

}

header>.user:hover>div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    animation-name: fade-in;
    /*动画名称*/
    animation-duration: 0.3s;
    /*动画持续时间*/
    -webkit-animation-name: fade-in;
    /*针对webkit内核*/
}



header>.user:hover {
    background: rgba(255, 255, 255, 0.400);
    border-radius: 0.8rem;

}

header>.user svg {
    fill: #fff;
    font-size: 1.7rem;
}

header>.user>img {

    height: 75%;
    border-radius: 2rem;

}

header .user>div>div {
    margin: 0.6rem 0rem;
    /* height: 2.5rem; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: stretch;
    /* align-items: stretch; */
    border-radius: 0.2rem;
    width: 90%;




}

header .user>div>div>div {

    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: stretch;
    /* border-left: 0.3rem solid #fff;  */
    margin: 0.5rem 0.5rem;



}

header .user>div>div:hover {
    background: rgba(255, 255, 255, 0.400);
}

@media screen and (max-width:991px) {
    header .navigation {
        display: none;
    }

    /* header .search-s {
        width: 40%;
    }
    header .search-s .search{
        width: 100%;
    } */

    header .logo {
        width: 15%;
    }

    header>.navigation-simplify {
        display: flex;

    }

    /* header>.user {
        display: none;
    } */


}

@media screen and (max-width:600px) {

    /* header .search-s .search{
        width: 100%;
    }
    header .search-s 
    {
        width: 50%;
    } */
    header .logos {

        min-width: 20%;

        justify-content: flex-start;


    }


}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    /*初始状态 透明度为0*/
    50% {
        opacity: 0;
    }

    /*过渡状态 透明度为0*/
    100% {
        opacity: 1;
    }

    /*结束状态 透明度为1*/
}

@-webkit-keyframes fade-in {

    /*针对webkit内核*/
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}