
html {
    font-size: 16px;
    font-family: sans-serif;

}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* font-family: cursive; */
    word-break: break-all;
}

.body ::-webkit-scrollbar {
    /*高宽分别对应横竖滚动条的尺寸*/
    width: 0.7rem;
    height:0.7rem;
}

/*滚动条里面小方块*/
.body ::-webkit-scrollbar-thumb {
    border-radius: 0;
    -webkit-box-shadow: inset 0 0 5px rgba(160, 160, 160, 0.502);
    background: #594dabb0;
    height: 2.5rem;
   


}
.body ::-webkit-scrollbar-thumb:hover {
    border-radius: 0;
    -webkit-box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.2);
    background: #0f0098a3;

}

.body ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px rgba(211, 211, 211, 0.062);
    border-radius: 0;
    background: #f7994656;
    border-left: 0.1rem solid rgb(105, 105, 105);
    width: 0.8rem;
   
}
.markdown-body svg{
    width: 100%;
  }

.body[theme='dark']{
    --color-canvas-default: #000000;
}
.body[theme='light']{
    color-scheme: light;
    --header-color: #242260bb;
    --header-font-color:  #ffffff;
    --header-border-color:  #c718537e;
    --header-srarch-border-color:  #e0e0e0;
    --header-border: #b54c4cc0;
    --header-svg: #cececec0;
    --header-hover-color: #607ebf;
    --header-hover-in-color: #3735c4cf;
    --layout-color: rgba(185, 185, 185, 0);
    
    --banner-color: #15158aaa;
    --banner-border-color: #ffffff;
    --banner-font-color: #ffffff;
    --banner-hover-color: #6c17c77b;

    --individualSpace-background-color: #9c46b64f;

    --createArticle-background-color:rgb(255, 255, 255);
}

.body {
    
    height: 100%;
    width:  100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    justify-items: center;
    align-items: center;
    background: rgba(0, 0, 0, 0);
    position: relative;
    top: 0;
    left: 0;
    /* backdrop-filter: blur(0.8rem); */
}
.body .backimg{
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    height: 100%; 
    width: 100%;
    object-fit:cover;
}


a {
    text-decoration: none;
    color: black;
}
.displaynone {
    display: none !important;
}

