.displayNone {
    display: none;
}

.markdownbox {

    min-height: 60vh;
    height: 40rem;
    width: 99%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 0rem 1rem;
    border: 0.1rem solid #000000;
    border-radius: 0.5em;
    box-shadow: 0.1rem 0.1rem 0.1rem 0.05rem rgba(0, 0, 0, 0.123);
    padding: 1rem 0.5rem;
    background: #9c46b64f;
    backdrop-filter: blur(0.01em);
    font-size: 1rem;

}

.auxiliary>div:hover svg {
    fill: white;
}

.auxiliary svg {
    width: 1.5em;
    height: 1.5em;
}

.auxiliary>div:hover {

    background: black;

}

.auxiliary {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0.5em 0;
    border-radius: 0.5em;
    background: rgba(255, 255, 255, 0.742);
    flex-wrap: wrap;
    backdrop-filter: blur(1em);
    border: 0.05em solid #000000;
}

.auxiliary>div {
    min-width: 2.2em;
    padding: 0.3em 0.3em;
    border-radius: 0.2em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0em 1em;
}


.original {

    width: 100%;
    height: 100%;
    //border-radius: 0.5em 0em 0em 0.5em;
    background: #ffffff00;

}

.originalTextarea {
    resize: none;
    border-radius: 0.1em;
    width: 100%;
    height: 100%;
    padding: 0.5em 0.5em;
    font-size: 1.2em;
    cursor: default;
    border: 0.05em solid #000000;
    //border-radius: 0.5em 0.5em 0.5em 0.5em;
}

.originalTextarea[data-scrollbar='off']::-webkit-scrollbar {
    /*高宽分别对应横竖滚动条的尺寸*/
    width: 0em;
    height: 0em;
}

.preview {
    width: 100%;
    height: 100%;
    background: rgb(165, 48, 48);
    border: 0.05em solid #000000;
    font-size: 1.2em;
    cursor: default;
    //border-radius: 0em 0.5em 0.5em 0em;
    overflow: auto;
}


.previewContent {

    box-sizing: border-box;
    height: 100%;
    padding: 0.5em 0.5em;

}

.previewContent code * {
    margin: 0.5em 0;
    font-size: 1.5em;
    line-height: 2em;
}

.content {
    width: 100%;
    height: calc(100% - 4em);
    display: flex;
    justify-self: center;
}

.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 0em 1em;
    border: 0.1em solid #000000;
    border-radius: 0.0em;
    box-shadow: 0.1em 0.1em 0.1em 0.05em rgba(0, 0, 0, 0.123);
    padding: 1% 0.5em;
    background: #ceb8d4;
    backdrop-filter: blur(0.01em);
}

.copy {
    height: 3em;
    width: 3em;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0.5em;
    right: 0em;
}

.inf {
    position: fixed;
    width: 12em;
    height: 3em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: calc(50vh - 1.5em);
    left: calc(50vw - 6em);
    padding: 0.5em.1em;
    border-radius: 0.5em;
    border: 0.1em solid rgba(23, 23, 23, 0.32);
    backdrop-filter: blur(1em);
    background-color: rgb(255, 255, 255);
    font-size: 1.3em;
    font-weight: 700;
    z-index: 1000;
    letter-spacing: 0.2em;

}

pre:hover .copy {
    display: flex;
}