.displayNone {
    display: none;
}

.YamArticleShow {
    height: auto;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    border-radius: 0.5em;
    box-shadow: 0.1em 0.1em 0.1em 0.05em rgba(0, 0, 0, 0.123);
    background: #ffffff00;
    /* backdrop-filter: blur(0.01em); */
    font-size: 1rem;

}

.MarkNav {
    width: 20em;
    height: 20 vh;
    min-height: 40em;
    background: #ffffff;
    border: 0.1em solid #000000;
    font-size: 1em;
}

.MarkNav:hover ::-webkit-scrollbar {
    width: 0.7em;
    height: 0.7em;
}

.MarkNav ::-webkit-scrollbar {
    width: 0em;
    height: 0em;
    background: rgba(255, 0, 0, 0);
}

.ReactMarkdown[data-marknav='on'] {
    position: sticky;
    width: calc(100% - 25em);
    height: 100%;
    border: 0.1em solid #ffffff;
    padding: 1em 1em;
    font-size: 1.1em;
    padding-bottom: 4rem;
    background: #ffffff;
    /* border-radius: 0.5em; */
    /* -webkit-backdrop-filter: blur(2em); */
    /* backdrop-filter: blur(1em); */
    margin: 0em 0em;
}

.ReactMarkdown[data-marknav='off'] {
    position: sticky;
    width: 100%;
    height: 100%;
    border: 0.1em solid #ffffff;
    padding: 1em 1em;
    font-size: 1.1em;
    padding-bottom: 4rem;
    background: #ffffff;
    /* border-radius: 0.5em; */
    /* -webkit-backdrop-filter: blur(2em); */
    /* backdrop-filter: blur(1em); */
    margin: 0em 0em;
}


.copy {
    height: 3em;
    width: 9em;
    display: none;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    top: 0.5em;
    right: 1em;
    background: rgba(255, 255, 255, 0);
}

.copy svg {
    width: 1.5em;
    height: 1.5em;
    fill: rgb(0, 0, 0)
}

pre:hover .copy {
    display: flex;
}

@media screen and (max-width:991px) {

    .MarkNav {
        display: none;
    }

    .ReactMarkdown[data-marknav='on'] {
        width: 100%;
    }

    .ReactMarkdown[data-marknav='off'] {
        width: 100%;
    }




}