.title {
    width: 100%;
    min-height: 1.3em;
    font-size:2.0em;
    font-weight: 700;
}
.title a:hover{
   color:rgb(150, 0, 236);
  cursor: default;
   
}

.synopsis {
    width: 100%;
    min-width: 6em;
    height: 4em;
    font-size: 1.3em;
    line-height: 2em;
    overflow: hidden;
    position: relative;
    border: 0.01em solid rgba(91, 90, 90, 0.174);
    border-radius: 0.4em; 
    padding: 0em 0.5em;
    box-shadow: 0.12em 0.1em 0.8em 0.12em rgba(91, 90, 90, 0.174);
    background: rgb(255, 255, 255);

}
.synopsis::after {
    background: linear-gradient(to right, rgba(255, 255, 255, 0.466), rgba(255, 255, 255, 0.829), rgba(255, 255, 255, 0.893));
    content: "";
    display: block;
    height: 2em;
    pointer-events: none;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 50%;
}

.label {
    display: flex;
    min-height: 2.0em;
    justify-content: space-between;
    font-size: 1.1em;
    width: 100%;
    align-items: center;
    flex-wrap: wrap;
}
.card {
    width: 40rem;
    min-height: 13em;
    max-width: 60em;
    padding: 0.5em 0.5em;
    border: 0.1em solid rgba(52, 52, 52, 0.267);
    border-radius: 0.8em;
    box-shadow: 0.12em 0.1em 0.5em 0.1em rgba(111, 111, 111, 0.267);
    background: rgba(255, 255, 255, 0.9);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    margin: 0.5em 1em;

}
@media screen and (max-width:991px) {
    .card{
        width: 98%;
    }
}

