.markdown-navigation {
    font-size: 1.3em;
    font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Helvetica", "Arial", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
    width: 100%;
    height: 100%;
    overflow: auto;
}

.markdown-navigation .title-anchor {
    display: flex;
    justify-content: start;
    align-items: center;
    color: #979797;
    transition: all 0.2s;
    margin: 0.8em 0;
    font-weight: lighter;
    line-height: 2em;
    padding-right: 1.8em;
    cursor: pointer;

}

.markdown-navigation .title-anchor:hover,
.markdown-navigation .title-anchor.active {
    background-color: #232323e0;
    text-decoration: inherit;
    color: #ffffff;
}

.markdown-navigation .title-anchor.active {
    color: #ffffff;
}

.markdown-navigation .title-anchor small {
    margin: 0 0.8em;
}

.markdown-navigation .title-level1 {
    color: #000;
    font-size: 1.2em;
    padding-left: 1em;
    font-weight: normal;
}

.markdown-navigation .title-level2 {
    color: #333;
    font-size: 1em;
    padding-left: 1em;
    font-weight: normal;
}

.markdown-navigation .title-level3 {
    color: #666;
    font-size: 0.8em;
    padding-left: 3em;
    font-weight: normal;
}

.markdown-navigation .title-level4 {
    color: #999;
    font-size: 0.72em;
    padding-left: 5em;
}

.markdown-navigation .title-level5 {
    color: #aaa;
    font-size: 0.72em;
    padding-left: 7em;
}

.markdown-navigation .title-level6 {
    color: #bbb;
    font-size: 0.72em;
    padding-left: 9em;
}
